<template>
  <div id="app">
    <h1>Danube.com</h1>
    <h3>Buy books, it's great!</h3>
    <router-view name="componentShop"></router-view>
    <router-view name="componentDetail"></router-view>
    <router-view name="componentSidebar"></router-view>
  </div>
</template>

<script>
import Shop from './components/Shop'
import Sidebar from './components/Sidebar'
import Detail from './components/Detail'

export default {
  name: 'App',
  components: { 'component-sidebar': Sidebar, 'component-Shop': Shop, 'component-detail': Detail }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
  margin-left: 10px;
}
</style>



