<template>
  <div class="shop">
    <!-- <p>{{ id }}</p> -->
    <h2>Top sellers</h2>
    <ul>
      <li
        class="preview"
        v-for="item in items"
        v-bind:key="item.TITLE"
        v-on:click="navigateToMessage(item.id)"
      >
        <div class="preview-title">{{ item.title }}</div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      items: [ {id: 1, title: 'This book'}, {id: 2, title: 'That book'} ],
      id: "books"
    };
  },
  mounted() {
    this.pullItems();
  },
  methods: {
    pullItems: function() {
      // eventually pull files from server
    },
    navigateToMessage: function(id) {
      this.$router.push({ path: `/books/${id}` }).catch(err => {
        this.$router.push({ path: "/" });
      });
    }
  }
};
</script>

<style scoped>
.shop {
  width: 75%;
  float: right;
}
.preview-title {
    font-weight: bold;
    margin: auto;
    text-align: center;
}
.preview {
    border: 1px solid #eee;
    background: #f9f9f9;
    padding: 4px;
}
li {
    display: inline-block;
    width: 200px;
    height: 110px;
    overflow: hidden;
    margin: 5px;
}
</style>



